.task {
    display: flex;
    justify-content: space-between;
    min-height: 48px;
    max-width: 1228px;
    margin: 8px 0 0 0;
    box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.08);
}

.checkbox {
    display: flex;
    width: 1%;
    padding-left: 12px;
    align-items: center;
    justify-content: center;
}
.checkbox input {
    cursor: pointer;
}
.body {
    align-items: center;
    margin: auto;
    width: 94%;
    padding-left: 10px;
    font-size: 14px;
    word-wrap: break-word;
}
.buttonCase {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 5%;
}
.button {
    width: 20px;
    height: 20px;
    cursor: pointer;
    border: none;
    background: none;
    padding: 0;
}

@media screen and (max-width: 896px) {
    .task {
        width: 100%;
    }
    .body {
        width: 85%;
    }
    .checkbox {
        width: 5%;
    }
    .buttonCase {
        width: 10%;
    }
}